import React from "react";
import style from "../css/Team.module.css";
import { TeamItem } from "../../components";
function Team() {
  return (
    <div className={style.wrapper}>
      <ul>
        {Array(4)
          .fill()
          .map((item, index) => (
            <li>
              <TeamItem />
            </li>
          ))}
      </ul>
    </div>
  );
}

export default Team;
