import "./App.css";
import {
  Dashboard,
  History,
  PayemntPage,
  Footer,
  CreateDocument,
  Team,
} from "./pages";
import { NavBar } from "./components";
import { useRef } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

function App() {
  const PageRef = useRef(null);
  return (
    <div className="App">
      <Router basename="PlatformX/">
        <Switch>
          <Route
            exact
            path="/"
            component={() => (
              <>
                <NavBar _Ref={PageRef} />
                <Dashboard />
                <History _Ref={PageRef} />
                <Team />
                <PayemntPage />
                <Footer />
              </>
            )}
          />{" "}
          <Route path="/CreateDocument" component={() => <CreateDocument />} />{" "}
        </Switch>{" "}
      </Router>{" "}
    </div>
  );
}

export default App;
